import React from "react";
import { useParams } from "react-router-dom";

import {
  TextField,
  Typography,
  Grid,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Checkbox,
  ListItemText,
  Input,
} from "@material-ui/core";
import RequiredMark from "./RequiredMark";
import { I18n, En, Fr } from "../I18n";
import { roleCodes } from "../../isoCodeLists";
import { camelToSentenceCase } from "../../utils/misc";
import translate from "../../utils/i18n";
import { QuestionText, SupplementalText } from "./QuestionStyles";

const Contact = ({ onChange, value, showRolePicker, disabled }) => {
  const { language } = useParams();
  const options = roleCodes;
  const optionLabels = roleCodes.map((e) => {
    if (e === "custodian") return "Metadata Contact";
    if (e === "owner") return "Data Contact";
    return camelToSentenceCase(translate(e, language));
  });

  return (
    <Grid container direction="column" spacing={2}>
      <Grid item xs>
        {showRolePicker && (
          <Grid item xs>
            <QuestionText>
              <En>What is the role of this contact?</En>
              <Fr>Quel est leur rôle?</Fr>.
              <SupplementalText>
                <En>
                  At least one Metadata Contact and one Data Contact are
                  required. Multiple roles may be selected for each contact. A
                  list of roles with descriptions can be found{" "}
                </En>
                <Fr>
                  Au moins un contact de métadonnées et un contact de données
                  sont requis. Une liste de rôles avec des descriptions peut
                  être trouvée{" "}
                </Fr>
                <a
                  href="http://registry.it.csiro.au/def/isotc211/CI_RoleCode"
                  // eslint-disable-next-line react/jsx-no-target-blank
                  target="_blank"
                >
                  <En>here</En>
                  <Fr>ici</Fr>
                </a>
              </SupplementalText>
            </QuestionText>
            <FormControl disabled={disabled} style={{ minWidth: 120 }}>
              <InputLabel
                id="demo-simple-select-label"
                style={{ marginLeft: "10px" }}
              >
                <I18n en="Choose" fr="Choisir" />
              </InputLabel>
              <Select
                multiple
                inputProps={{ variant: "outlined" }}
                name="role"
                input={<Input />}
                fullWidth
                value={value.role || []}
                renderValue={(selected) =>
                  selected
                    .map(
                      (valueCode) => optionLabels[options.indexOf(valueCode)]
                    )
                    .join(", ")
                }
                onChange={onChange}
              >
                {options.map((v, i) => (
                  <MenuItem key={v} value={v}>
                    <Checkbox checked={value.role && value.role.includes(v)} />
                    <ListItemText
                      primary={
                        <span style={i < 2 ? { fontWeight: "bold" } : {}}>
                          {optionLabels[i]}
                          {i < 2 && <RequiredMark />}
                        </span>
                      }
                    />
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Grid>
        )}

        <Grid
          container
          direction="column"
          spacing={1}
          style={{ marginTop: "10px" }}
        >
          {/* Organization */}
          <Grid item xs>
            <QuestionText>
              <En>Provide any information about the organization</En>
              <Fr>Fournir toute information sur l'organisation ;</Fr>
            </QuestionText>
          </Grid>
          <Grid item xs style={{ marginleft: "10px" }}>
            <TextField
              label={<I18n en="Organization name" fr="Nom de l'organisation" />}
              name="orgName"
              value={value.orgName}
              onChange={onChange}
              onBlur={onChange}
              disabled={disabled}
              fullWidth
            />
          </Grid>
          <Grid item xs>
            <TextField
              label={<I18n en="URL" fr="URL" />}
              name="orgURL"
              value={value.orgURL}
              onChange={onChange}
              disabled={disabled}
              fullWidth
            />
          </Grid>
          <Grid item xs>
            <TextField
              label={<I18n en="Address" fr="Adresse" />}
              name="orgAdress"
              value={value.orgAdress}
              onChange={onChange}
              disabled={disabled}
              fullWidth
            />
          </Grid>
          <Grid item xs>
            <TextField
              label={<I18n en="City" fr="Ville" />}
              name="orgCity"
              value={value.orgCity}
              onChange={onChange}
              disabled={disabled}
              fullWidth
            />
          </Grid>
          <Grid item xs>
            <TextField
              label={<I18n en="Country" fr="Pays" />}
              name="orgCountry"
              value={value.orgCountry}
              onChange={onChange}
              disabled={disabled}
              fullWidth
            />
          </Grid>
          <Grid item xs>
            <TextField
              label={<I18n en="Email" fr="Email" />}
              name="orgEmail"
              value={value.orgEmail}
              onChange={onChange}
              fullWidth
              disabled={disabled}
            />{" "}
          </Grid>
        </Grid>
      </Grid>

      <Grid item xs>
        {/* Individual */}
        <Typography>
          <En>Provide any information about the individual</En>
          <Fr>Fournir toute information sur l'individual</Fr>
        </Typography>
        <TextField
          label={<I18n en="Individual Name" fr="Nom de l'individu" />}
          name="indName"
          value={value.indName}
          onChange={onChange}
          disabled={disabled}
          fullWidth
        />

        <TextField
          label={<I18n en="Position" fr="Position" />}
          name="indPosition"
          value={value.indPosition}
          onChange={onChange}
          disabled={disabled}
          fullWidth
        />
        <TextField
          label={<I18n en="Email" fr="Email" />}
          name="indEmail"
          value={value.indEmail}
          onChange={onChange}
          disabled={disabled}
          fullWidth
        />
      </Grid>
    </Grid>
  );
};

export default Contact;
